import { __decorate } from "tslib";
import zh_CN from 'ant-design-vue/lib/locale-provider/zh_CN';
import moment from 'moment';
import { LocaleProvider } from 'ant-design-vue';
import { Component, Vue } from 'vue-property-decorator';
import 'moment/locale/zh-cn';
moment.locale('en');
let App = class App extends Vue {
    constructor() {
        super(...arguments);
        this.locale = zh_CN;
    }
    mounted() {
        this.$bus.$on('change-lang', (lang) => {
            switch (lang) {
                case 'zh-cn':
                    this.locale = zh_CN;
                    moment.locale('zh-cn');
                    this.$i18n.locale = 'zh';
                    break;
                case 'en':
                    this.locale = null;
                    this.$i18n.locale = 'en';
                    moment.locale('en');
                    break;
                default:
                    this.locale = zh_CN;
                    moment.locale('zh-cn');
            }
        });
    }
};
App = __decorate([
    Component({
        components: {
            'a-locale-provider': LocaleProvider
        }
    })
], App);
export default App;
