import { __decorate } from "tslib";
import { updateTheme } from '../../utils/common';
import { Vue, Component } from 'vue-property-decorator';
import { Dropdown, Menu, Avatar, Icon, Switch } from 'ant-design-vue';
let ThemeConfig = class ThemeConfig extends Vue {
    constructor() {
        super(...arguments);
        this.colors = ['#F5222D', '#FA541C', '#FAAD14', '#13C2C2', '#52C41A', '#1890FF', '#2F54E8', '#722ED1'];
        this.activeTheme = '#2F54E8';
    }
    changeTheme(color) {
        this.activeTheme = color;
        localStorage.setItem('local_theme', color);
        updateTheme(color);
    }
    mounted() {
        // const init_theme = localStorage.getItem('local_theme')
        // this.activeTheme = (init_theme || '#2F54E8')
        // updateTheme(this.activeTheme)
    }
    changeMenuTheme(val) { this.$emit('changeLeftBar', val); }
};
ThemeConfig = __decorate([
    Component({
        components: {
            'a-icon': Icon,
            'a-switch': Switch,
            'a-avatar': Avatar,
            'a-dropdown': Dropdown,
            'a-menu-item': Menu.Item,
            'a-menu-divider': Menu.Divider,
            'a-menu': Menu
        }
    })
], ThemeConfig);
export default ThemeConfig;
